.anypage {
    &__inner {
        padding: 50px;
        text-align: center;
    }
    &__image {
        img {
            max-width: 100%;
        }
        margin-bottom: 20px;
    }
    &__title {
        text-transform: uppercase;
        font-size: 30px;
        font-weight: 700;
        @media(max-width: 768px) {
            font-size: 20px;
        }
        @media(max-width: 450px) {
            font-size: 16px;
        }
    }
}